var express = require("express");

var path = require("path");

var proxy = require("http-proxy-middleware");

var render = require("./render");

var Router = express.Router;

function renderMiddleware(payload, req, res, next) {
  render(payload, req, res, next);
}

function createSSRMiddleware(middleware) {
  var router = Router();

  if (process.env.NODE_ENV === "production") {
    router.use("/static", express.static(path.join(process.cwd(), "build/static")));
  } else {
    router.use(["/static", "/sockjs-node"], proxy({
      target: "http://localhost:".concat(process.env.REACT_APP_DEV_SERVER_PORT),
      ws: true
    }));
  }

  router.use(middleware);
  router.use(renderMiddleware);
  return router;
}

module.exports = {
  createSSRMiddleware: createSSRMiddleware
};